.switch-theme-check {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.switch-theme-check-desc {
  position: relative;
}

.switch-theme-check-desc:before {
  content: "";
  width: 40px;
  height: 20px;
  background-image: linear-gradient(#4f2ee8, #dcdcdc), linear-gradient(#fff, #f7f7f7);
  border-radius: 50px;
  display: block;
}

.switch-theme-check-desc:after {
  content: "";
  width: 18px;
  height: 18px;
  background-color: #fff;
  border-radius: 50%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .3);
}

.switch-theme-check.checked + .switch-theme-check-desc:after {
  transform: translate(20px);
}

.switch-theme-check.checked + .switch-theme-check-desc:before {
  background-image: linear-gradient(#4f2ee8, #686868), linear-gradient(#fff, #f7f7f7);
}

